import { Component, OnInit } from '@angular/core';
import {ResetHttpService} from '../../../share/util/reset-http.service';
import {UtilServeService} from '../../../share/util/util-serve.service';
import {UrlServeService} from '../../../share/util/url-serve.service';
import {NzMessageService, NzModalService} from 'ng-zorro-antd';
import {ModalClassifyProductComponent} from '../modal-classify-product/modal-classify-product.component';
import {FormBuilder} from '@angular/forms';

@Component({
  selector: 'app-product-manger',
  templateUrl: './product-manger.component.html',
  styleUrls: ['./product-manger.component.scss']
})
export class ProductMangerComponent implements OnInit {
  dataSet = [];
  validateForm;
  isEnablemodal;
  child;
  //树形表格
  expandDataCache = {};

  constructor(
    private http:ResetHttpService,
    private util:UtilServeService,
    private url:UrlServeService,
    private msg:NzMessageService,
    private fb:FormBuilder,
    private modalService:NzModalService
  ) {
    this.validateForm = this.fb.group({
      state:[],
      phone:[]
    })
  }
  ngOnInit(): void {
    this.getData();
  }
  //获取组件
  get state(){
    return this.validateForm.controls.state;
  }get phone(){
    return this.validateForm.controls.phone;
  }
  //搜索
  searchMore(){
    this.getData(this.state.value,this.phone.value);

  }
  //获取数据
  getData(status = '',name = ''){
    this.http.get(this.url.productTypeList,{
      page:'',
      rows:'',
      status:status,
      name:name,
    },data=>{
      console.log(data);
      if(data.code == '001000'){
        this.dataSet = data.response;
        //树形组件
        this.dataSet.forEach(item => {
          this.expandDataCache[ item.id ] = this.convertTreeToList(item);
        });
      }else{
        this.msg.error(data.message);
      }
    })
  }
  /*
 * 表格操作
 * */

  //停用
  isEnable(data){
    console.log(data);
    this.isEnablemodal = this.modalService.create({
      nzTitle: '',
      nzContent: '停用商品大分类时，所属中和小分类也将被停用。停用后，前端用户将无法查看小分类下的商品，也无法购买商品！确认停用吗？<br><br>确认停用吗',
      nzClosable: false,
      nzOnOk: () => new Promise((resolve) => {
        this.http.get(this.url.updateProductType,{
          id:data.id,
          status:'N'
        },res=>{
            console.log(res);
            if(res.code == '001000'){
              this.msg.success('操作成功！');
              this.getData();
              this.isEnablemodal.destroy();
            }
        },err=>{
          console.log(err);
        });
      }),
      nzOnCancel:() => new Promise((resolve) =>{
        this.isEnablemodal.nzCancelLoading =false;
        this.isEnablemodal.destroy();
      })
    });
  }
  //启用
  isable(data){
    console.log(data);
    this.http.get(this.url.updateProductType,{
      id:data.id,
      status:'Y'
    },data=>{
      if(data.code === '001000'){
        this.getData();
        this.msg.success('操作成功');
      }else{
        this.msg.error(data.message);
      }
    },err=>{
      console.log(err);
    })

  }
  //删除
  isDele(data){

  }
  //创建模态窗 创建分类
  createModal(data): void {
    this.validateForm.reset();
    var modal = this.modalService.create({
      nzTitle: '',
      nzContent: ModalClassifyProductComponent,
      nzClosable: false,
      nzComponentParams:data,
      nzFooter:null
    });
    modal.afterOpen.subscribe(() => console.log(''));
    modal.afterClose.subscribe((result) => {
      if(result == 'success'){
        this.getData();
      }
    });

  }
  //跳转
  goPager(option){
    this.util.goPage(option);
  }
  //树形表格

  collapse(array, data, $event: boolean): void {
    if ($event === false) {
      if (data.children) {
        data.children.forEach(d => {
          const target = array.find(a => a.id === d.id);
          target.expand = false;
          this.collapse(array, target, false);
        });
      } else {
        return;
      }
    }
  }

  convertTreeToList(root: object) {
    const stack = [];
    const array = [];
    const hashMap = {};
    stack.push({ ...root, level: 0, expand: false });

    while (stack.length !== 0) {
      const node = stack.pop();
      this.visitNode(node, hashMap, array);
      if (node.children) {
        for (let i = node.children.length - 1; i >= 0; i--) {
          stack.push({ ...node.children[ i ], level: node.level + 1, expand: false, parent: node });
        }
      }
    }

    return array;
  }

  visitNode(node, hashMap: object, array): void {
    if (!hashMap[ node.id ]) {
      hashMap[ node.id ] = true;
      array.push(node);
    }
  }
}
