import { Component, OnInit } from '@angular/core';
import {NzMessageService, UploadFile} from 'ng-zorro-antd';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ResetHttpService} from '../../../share/util/reset-http.service';
import {UrlServeService} from '../../../share/util/url-serve.service';
import {ActivatedRoute} from '@angular/router';
declare var require;
var E = require('wangeditor');
@Component({
  selector: 'app-product-list-edit',
  templateUrl: './product-list-edit.component.html',
  styleUrls: ['./product-list-edit.component.scss']
})
export class ProductListEditComponent implements OnInit {
  validateForm: FormGroup;
  loading = false;
  avatarSmallUrl: string;
  selectedFirst;
  selectedTwo;
  selectedThree;
  imgUrl;
  smallImg;
  edit;
  life = false;
  firstData;
  twoData;
  threeData;
  pro_id = [];
  allPicUrl = [];
  radioValue;
  params;
  num = 0;
  //多图上传
  fileList = [];
  previewImage = '';
  previewVisible = false;
  removeUrl;
  constructor(private route :ActivatedRoute,private fb: FormBuilder,private http: ResetHttpService,private msg: NzMessageService,private url:UrlServeService) {
    this.validateForm = this.fb.group({
      pro_name: [ '', [ Validators.required ] ],
      supplyer: [ '', [ Validators.required ] ],
      pro_price: [ '', [ Validators.required ] ],
      pro_profits: [ '', [ Validators.required ] ],
      pro_sig_price: [ '', [ Validators.required ] ],
      pro_vip: [ '', [ Validators.required ] ],
      inventory: [ '', [ Validators.required ] ],
      selectT: [ '', [ Validators.required ] ],
      selectF: [ '', [ Validators.required ] ],
      selectThr: [ '', [ Validators.required ] ],
      isable:['',[Validators.required]],
      pro_type:['',[Validators.required]]
    });
    this.imgUrl = this.url.commentBaseUrl+this.url.imgUpLoad;
  }

  ngOnInit() {
    // 创建编辑器
    this.edit = new E('#edit')
    this.edit.create();
    this.getClassify();
    this.route.queryParams.subscribe((data)=>{
      this.params = JSON.parse(data.data).data;
      this.validateForm.controls['pro_name'].setValue(this.params.name);
      this.validateForm.controls['supplyer'].setValue('西安');
      this.validateForm.controls['pro_price'].setValue(this.params.cost_price);
      this.validateForm.controls['pro_profits'].setValue(this.params.profit_rate*100);
      this.validateForm.controls['pro_sig_price'].setValue(this.params.sell_price);
      this.validateForm.controls['inventory'].setValue(this.params.stock_quantity);
      this.radioValue = this.params.is_home_page;
      this.validateForm.controls['pro_type'].setValue(this.params.type);
      this.validateForm.controls['pro_vip'].setValue(this.params.vip_sell_price);
      this.avatarSmallUrl = this.params.big_picture;
      this.smallImg = this.params.big_picture;
      this.edit.txt.html(this.params.content);
      var fileList = this.params.small_picture.split(',');
      fileList.forEach((v,k)=>{
        var obj = {};
        obj['url'] = v;
        obj['uid'] = -k;
        obj['name'] = k;
        obj['status'] = 'done';
        this.fileList.push(obj);
      });
      this.allPicUrl = this.params.small_picture.split(',');
      console.log(this.allPicUrl,this.fileList);
    })
  }
  //获取产品分类
  getClassify(){
    this.http.get(this.url.productTypeList,{

    },data=>{
      // console.log(data);
      if(data.code == '001000'){
        this.firstData = data.response;
        this.selectedFirst = this.params.first_type_id;
      }else{
        console.log(data.message);
      }
    },err=>{
      console.log(err);
    })
  }
//提交
  submitForm = ($event, value) => {
    $event.preventDefault();
    for (const key in this.validateForm.controls) {
      this.validateForm.controls[ key ].markAsDirty();
      this.validateForm.controls[ key ].updateValueAndValidity();
    }
    if(!this.validateForm.valid) return;
    if(!this.smallImg || this.fileList.length <= 0) {
      this.allPicUrl = [];
      this.msg.error('请上传图片！');
      return;
    }
    var content = this.edit.txt.html();
    if(content === '<p><br></p>'){
      console.log(12);
      this.life = true;
      return;
    }else{
      this.life = false;
    }
    if(this.pro_id.length < 0){
      this.msg.error('请选择分类！');
      return;
    }
    this.http.get(this.url.updateProduct,{
      id:this.params.id,
      name:this.validateForm.controls['pro_name'].value,
      first_type_id:this.pro_id[0],
      second_type_id:this.pro_id[1],
      third_type_id:this.pro_id[2],
      support_id:this.validateForm.controls['supplyer'].value,
      cost_price:this.validateForm.controls['pro_price'].value,
      sell_price:this.validateForm.controls['pro_sig_price'].value,
      profit_rate:this.validateForm.controls['pro_profits'].value,
      vip_sell_price:this.validateForm.controls['pro_vip'].value,
      stock_quantity:this.validateForm.controls['inventory'].value,
      big_picture:this.smallImg,
      small_picture:this.allPicUrl.join(','),
      is_home_page:this.validateForm.controls['isable'].value,
      content:content,
      type:this.validateForm.controls['pro_type'].value
    },data=>{
      // console.log(data);
      if(data.code == '001000'){
        this.msg.success('修改成功！');
        history.back();
      }else{
        this.msg.error(data.message);
      }
    },err=>{
      this.msg.error(err);
    })
  };
  //取消
  cancleForm(e: MouseEvent): void {
    history.back();
  }
  //上传图片
  beforeUploadSmall = (file: File) => {
    const isJPG = file.type === 'image/jpeg';
    const isPng = file.type === 'image/png';
    if(!(isPng || isJPG)){
      this.msg.error('只允许上传JPG 或者 PNG格式的图片');
    }
    const isLt2M = file.size / 1024 / 1024 < 1;
    if (!isLt2M) {
      this.msg.error('大小不能超过2MB!');
    }
    return (isJPG || isPng) && isLt2M;
  }

  private getBase64(img: File, callback: (img: {}) => void): void {
    const reader = new FileReader();
    reader.addEventListener('load', () => callback(reader.result));
    reader.readAsDataURL(img);
  }

  handleChangeSmall(info: { file: UploadFile }): void {
    if (info.file.status === 'uploading') {
      this.loading = true;
      return;
    }
    if (info.file.status === 'done') {
      if(info.file.response.code == '001000'){
        this.smallImg = info.file.response.response.filePath;
      }
      // Get this url from response in real world.
      this.getBase64(info.file.originFileObj, (img: string) => {
        console.log(img);
        this.loading = false;
        this.avatarSmallUrl = img;
      });
    }
  }
  //多图上传
  handlePreview = (file: UploadFile) => {
    this.previewImage = file.url || file.thumbUrl;
    this.previewVisible = true;
  };
  handleMore(info: { file: UploadFile }){
    if (info.file.status === 'done') {
      if(info.file.response.code == '001000'){
        this.allPicUrl.push(info.file.response.response.filePath);
      }
    }
  }
  deleImg(file){
    return false;
  }
  //三级
  firstChange($event){
    this.selectedTwo = null;
    if($event){
      this.pro_id = [];
      this.firstData.forEach((v,k)=>{
        if(v['product_type_id'] == $event){
          this.twoData = v['children'];
        }
      })
      this.pro_id.push($event);
    }
    this.num++;
  }
  twoChange($event){
    this.selectedThree = null;
    if(this.num <= 2){
      $event = this.params.second_type_id;
      this.selectedTwo = this.params.second_type_id;
      this.selectedThree = this.params.third_type_id;
    }
    if($event && this.twoData){
      this.twoData.forEach((v,k)=>{
        if(v['product_type_id'] == $event){
          this.threeData = v['children'];
        }
      });
      this.pro_id.push($event);
    }
  }
  threeChange($event){
    if(this.num <= 2){
      $event = this.params.third_type_id;
      this.selectedThree = this.params.third_type_id;
    }
    if($event){
      this.pro_id.push($event);
    }
    this.pro_id = this.uniq(this.pro_id);
    console.log(this.pro_id);
  }
  //数组去重
  uniq(array){
    var temp = [];
    var index = [];
    var l = array.length;
    for(var i = 0; i < l; i++) {
      for(var j = i + 1; j < l; j++){
        if (array[i] === array[j]){
          i++;
          j = i;
        }
      }
      temp.push(array[i]);
      index.push(i);
    }
    return temp;
  }
}
