import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {UrlServeService} from '../../../share/util/url-serve.service';
import {NzMessageService, NzModalRef, UploadFile} from 'ng-zorro-antd';
import {UtilServeService} from '../../../share/util/util-serve.service';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {ResetHttpService} from '../../../share/util/reset-http.service';

@Component({
  selector: 'app-edit-product',
  templateUrl: './edit-product.component.html',
  styleUrls: ['./edit-product.component.scss']
})
export class EditProductComponent implements OnInit {
  type;
  validateForm: FormGroup;
  avatarBiglUrl;
  patt = /\w{6,12}/;
  smallImg = '';
  loading:boolean;
  imgUrl = this.url.commentBaseUrl+this.url.imgUpLoad;
  life:boolean;
  newData;
  //处理分类所属
  nzOptions;
  productId;
  public values: any[] = null;
  constructor(private util:UtilServeService,private fb: FormBuilder,private http: ResetHttpService,private router:Router,private url :UrlServeService,private msg:NzMessageService) {
    this.initForm();
  }

  ngOnInit() {
    if(this.type != 'b'){
      this.life = true;
    }
    this.getClassfy();
  }
  initForm(){
    this.validateForm = this.fb.group({
      proName: [{value:'',disabled:this.life} ,Validators.required],
      proSort : [{ value:'',disabled:this.life},Validators.required],
      classify:['',[Validators.required ]]
    });
  }
  //获取分类
  getClassfy(){
    this.http.get(this.url.productTypeList,{
      page:'',
      rows:'',
      status:'',
      name:''
    },data=>{
      console.log(data);
      if(data.code == '001000'){
        this.editData(data.response);
        if(this.type == 'm'){
          this.getFirst(this.newData);
        }else if(this.type == 's'){
          this.getSecond(this.newData);
        }
        this.nzOptions = this.newData;
        console.log(this.nzOptions);
      }else{
        console.log(data.message);
      }
    },err=>{
      console.log(err);
    })
  }
  //提交
  submitForm = ($event, value) => {
    $event.preventDefault();
    if(this.type != 'b'){
      for (const key in this.validateForm.controls) {
        this.validateForm.controls[ key ].markAsDirty();
        this.validateForm.controls[ key ].updateValueAndValidity();
      }
      if(!this.validateForm.valid) {
        return;
      }
    }else{
      this.validateForm.controls[ 'proName' ].markAsDirty();
      this.validateForm.controls[ 'proName' ].updateValueAndValidity();
      this.validateForm.controls[ 'proSort' ].markAsDirty();
      this.validateForm.controls[ 'proSort' ].updateValueAndValidity();
      if (this.validateForm.controls[ 'proSort' ].invalid || this.validateForm.controls[ 'proName' ].invalid) return;
    }

    if(!this.smallImg){
      this.msg.error('请上传图片！');
      return;
    }
    if(this.type == 'b'){
      this.firstClassify();
    }else if(this.type =='m'){
      this.secondClassify();
    }else{
      this.threeClassify();
    }
  };
  //大分类
  firstClassify(){
    this.http.get(this.url.addProductType, {
      name:this.validateForm.controls['proName'].value,
      sort:this.validateForm.controls['proSort'].value,
      first_type_id:'0',
      second_type_id:'',
      third_type_id:'',
      picture:this.smallImg,
      pid:'0'
    },(data)=>{
      console.log(data);
      if(data.code == '001000'){
        this.msg.success('添加成功！！');
        history.back();
      }else{
        console.log(data.message);
      }
    },err=>{
      console.log(err);
    })
  }
  //二级分类
  secondClassify(){
    if(!this.productId){
      return;
    }
    this.http.get(this.url.addProductType, {
      name:this.validateForm.controls['proName'].value,
      sort:this.validateForm.controls['proSort'].value,
      first_type_id:this.productId[0],
      second_type_id:'0',
      third_type_id:'',
      picture:this.smallImg,
      pid:this.productId[0]
    },(data)=>{
      console.log(data);
      if(data.code == '001000'){
        this.msg.success('添加成功！！');
        history.back();
      }else{
        this.msg.error(data.message);
      }
    },err=>{
      console.log(err);
    })
  }
  //三级分类
  threeClassify(){
    this.http.get(this.url.addProductType, {
      name:this.validateForm.controls['proName'].value,
      sort:this.validateForm.controls['proSort'].value,
      first_type_id:this.productId[0],
      second_type_id:this.productId[1],
      third_type_id:'0',
      picture:this.smallImg,
      pid:this.productId[1]
    },(data)=>{
      console.log(data);
      if(data.code == '001000'){
        this.msg.success('添加成功！！');
        history.back();
      }else{
        this.msg.error(data.message);
      }
    },err=>{
      console.log(err);
    })
  }
  //取消
  cancleForm(e: MouseEvent): void {
    history.back();
  }
  //验证密码
  checkPhone = (control:FormControl): {[ s: string ]: boolean} =>{
    if(!control.value){
      return {required:true}
    }else if(!this.patt.test(control.value)){
      return { error: true };
    }
  }
  //上传图片
  beforeUpload(file:File){
    const isJPG = file.type === 'image/jpeg';
    const isPng = file.type === 'image/png';
    if(!(isPng || isJPG)){
      this.msg.error('只允许上传JPG 或者 PNG格式的图片');
    }
    const isLt2M = file.size / 1024 / 1024 < 1;
    if (!isLt2M) {
      this.msg.error('大小不能超过2MB!');
    }
    return (isJPG || isPng) && isLt2M;
  }

  private getBase64(img: File, callback: (img: {}) => void): void {
    const reader = new FileReader();
    reader.addEventListener('load', () => callback(reader.result));
    reader.readAsDataURL(img);
  }

  handleChange(info: { file: UploadFile }): void {
    if (info.file.status === 'uploading') {
      this.loading = true;
      return;
    }
    if (info.file.status === 'done') {
      if(info.file.response.code == '001000'){
        this.smallImg = info.file.response.response.filePath;
      }
      // Get this url from response in real world.
      this.getBase64(info.file.originFileObj, (img: string) => {
        console.log(img);
        this.loading = false;
        this.avatarBiglUrl = img;
      });
    }
  }
  //处理分类所属

  public onChanges(values: any): void {
    this.productId = values;
    if(values && this.life){
      this.life = false;
      // this.initForm();
    }
  }
  //转换当前的数据
  editData(data){
    for( let item in data){
      data[item]['label'] = data[item]['name'];
      data[item]['value'] = data[item]['product_type_id'];
      if(data[item]['children']){
        for( let itemj in data[item]['children']){
          data[item]['children'][itemj]['label'] = data[item]['children'][itemj]['name'];
          data[item]['children'][itemj]['value'] = data[item]['children'][itemj]['product_type_id'];
          if(data[item]['children'][itemj]['children']){
            for(let iteml in data[item]['children'][itemj]['children']){
              data[item]['children'][itemj]['children'][iteml]['label'] = data[item]['children'][itemj]['children'][iteml]['name'];
              data[item]['children'][itemj]['children'][iteml]['value'] = data[item]['children'][itemj]['children'][iteml]['product_type_id'];
              data[item]['children'][itemj]['children'][iteml]['isLeaf'] = true;
            }
          }else{
            data[item]['children'][itemj]['isLeaf'] = true;
          }
        }
      }else{
        data[item]['isLeaf'] = true;
      }
    }
    this.newData = data;
  }
  //获取一级
  getFirst(data){
    for(let item in data){
      if(data[item]['children']){
        delete data[item]['children'];
        data[item]['isLeaf'] = true;
      }
    }
    this.newData = data;
  }
  //获取二级
  getSecond(data){
    debugger
    for(let item in data){
      if(data[item]['children']){
        for(let itemi in data[item]['children']){
          if(data[item]['children'][itemi]['children']){
            delete data[item]['children'][itemi]['children'];
            data[item]['children'][itemi]['isLeaf'] = true;
          }
        }
      }else{
        data.splice(item,1);
        this.getSecond(data);
      }
    }
    this.newData = data;
  }
}
