import { Component, OnInit } from '@angular/core';
import {UtilServeService} from '../../../share/util/util-serve.service';
import {UrlServeService} from '../../../share/util/url-serve.service';
import {ResetHttpService} from '../../../share/util/reset-http.service';
import {NzMessageService, NzModalService} from 'ng-zorro-antd';
import {FormBuilder, Validators} from '@angular/forms';

@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.scss']
})
export class ProductListComponent implements OnInit {

  allChecked = false;
  indeterminate = false;
  displayData = [];
  allSelecValue;
  selectedFirst;
  selectedTwo;
  selectedThree;
  data = [];
  validateForm;
  firstData;
  twoData;
  threeData;
  pro_id = [];
  constructor(
    private util:UtilServeService,
    private url:UrlServeService,
    private http:ResetHttpService,
    private msg:NzMessageService,
    private fb:FormBuilder,
    private modal:NzModalService
  ) {
    this.validateForm = this.fb.group({
      selectT: [ ],
      selectF: [],
      selectThr: [ ],
      state:[],
      name:[],

    })
  }
  //获取组件
  get name(){
    return this.validateForm.controls.name;
  }
  get state(){
    return this.validateForm.controls.state;
  }

  ngOnInit(): void {
    this.getData();
    this.getClassify();
  }
  //获取产品列表
  getData(name = '',first = '',second = '',three = '',status = ''){
    this.http.get(this.url.productList,{
      status:status,
      name:name,
      first_type_id:first,
      second_type_id:second,
      third_type_id:three
    },data=>{
      // console.log(data);
      if(data.code == '001000'){
        this.editData(data.response);
      }else{
        this.msg.error(data.message);
      }
    },err=>{
      console.log(err);
    })
  }
  currentPageDataChange($event: Array<{ name: string; age: number; address: string; checked: boolean; disabled: boolean; }>): void {
    this.displayData = $event;
    this.refreshStatus();
  }

  refreshStatus(): void {
    const allChecked = this.displayData.filter(value => !value.disabled).every(value => value.checked === true);
    const allUnChecked = this.displayData.filter(value => !value.disabled).every(value => !value.checked);
    this.allChecked = allChecked;
    this.indeterminate = (!allChecked) && (!allUnChecked);
    console.log(this.allChecked)
  }

  checkAll(value: boolean): void {
    this.displayData.forEach(data => {
      if (!data.disabled) {
        data.checked = value;
      }
    });
    this.refreshStatus();
  }
  //搜索
  searchMore(){
    this.getData(this.name.value,this.pro_id[0],this.pro_id[1],this.pro_id[2],this.state.value);
  }
  //获取产品分类
  getClassify(){
    this.http.get(this.url.productTypeList,{

    },data=>{
      // console.log(data);
      if(data.code == '001000'){
        this.firstData = data.response;
      }else{
        console.log(data.message);
      }
    },err=>{
      console.log(err);
    })
  }

//跳转
  goPager(option){
    this.util.goPage(option);
  }
  //批量操作
  moreUpdate(){
    var allId = [];
    this.data.forEach((v,k)=>{
      if(v['checked']){
        allId.push(v.id);
      }
    });
    if(this.allSelecValue == 's'){
      this.http.get(this.url.updateProfesser,{
        ids:allId.join(','),
        status:'Y'
      },data=>{
        // console.log(data);
        if(data.code == '001000'){
          this.msg.success('修改成功！');
          this.getData();
        }else{
          this.msg.error(data.message);
        }
      },err=>{
        console.log(err);
      })
    }else if(this.allSelecValue == 'x'){
      this.http.get(this.url.updateProfesser,{
        ids:allId.join(','),
        status:'N'
      },data=>{
        // console.log(data);
        if(data.code == '001000'){
          this.msg.success('修改成功！');
          this.getData();
        }else{
          this.msg.error(data.message);
        }
      },err=>{
        console.log(err);
      })
    }
  }
  //删除
  deleModal(data): void {
    var modal = this.modal.confirm({
      nzTitle: '',
      nzContent: '确认删除吗？',
      nzOnOk: () => new Promise((resolve, reject) => {
        //请求数据
        this.http.get(this.url.deleteproduct,{
          id:data.id
        },data=>{
          if(data.code == '001000'){
            this.msg.success('删除成功!');
            this.getData();
          }
        },err=>{
         console.log(err);
        })
        modal.destroy();
      }).catch(() => console.log('Oops errors!'))
    });
  }
  //上下架
  isEnable(data,state){
    var str;
    if(state == 'N'){
      str = '下架商品，前端用户将无法继续查看和购买！<br>确认下架吗？'
    }else{
      str = '确认上架吗？'
    }
    var modal = this.modal.confirm({
      nzTitle: '',
      nzContent: str,
      nzOnOk: () => new Promise((resolve, reject) => {
        //请求数据
        this.http.get(this.url.updateProfesser,{
          ids:data.id,
          status:state
        },data=>{
          if(data.code == '001000'){
            this.msg.success('修改成功!');
            this.getData();
          }
        },err=>{
          console.log(err);
        });
        modal.destroy();
      }).catch(() => console.log('Oops errors!'))
    });
  }
  //是否推荐到首页
  updataIsHome(data,state){
    var modal = this.modal.confirm({
      nzTitle: '',
      nzContent: '是否确认进行操作？',
      nzOnOk: () => new Promise((resolve, reject) => {
        //请求数据
        this.http.get(this.url.updateProfesser,{
          id:data.id,
          is_home_page:state
        },data=>{
          if(data.code == '001000'){
            this.msg.success('修改成功!');
            this.getData();
          }
        },err=>{
          console.log(err);
        });
        modal.destroy();
      }).catch(() => console.log('Oops errors!'))
    });
  }
  //数据转换
  editData(data){
    for(let item in data){
      data[item]['checked'] = false;
    }
    this.data = data;
  }
  //三级
  firstChange($event){
    this.selectedTwo = null;
    if($event){
      this.pro_id = [];
      this.firstData.forEach((v,k)=>{
        if(v['product_type_id'] == $event){
          this.twoData = v['children'];
        }
      })
      this.pro_id.push($event);
    }
  }
  twoChange($event){
    this.selectedThree = null;
    if($event){
      this.twoData.forEach((v,k)=>{
        if(v['product_type_id'] == $event){
          this.threeData = v['children'];
        }
      })
      this.pro_id.push($event);
    }
  }
  threeChange($event){
    if($event){
      this.pro_id.push($event);
    }
    this.pro_id = this.uniq(this.pro_id);
    console.log(this.pro_id)
  }
  //数组去重
  uniq(array){
    var temp = [];
    var index = [];
    var l = array.length;
    for(var i = 0; i < l; i++) {
      for(var j = i + 1; j < l; j++){
        if (array[i] === array[j]){
          i++;
          j = i;
        }
      }
      temp.push(array[i]);
      index.push(i);
    }
    console.log(index);
    return temp;
  }
}
